import { Component, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
} from "@angular/forms";
import { LoginService } from "../login.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  isSubmitted = false;
  constructor(
    private fb: FormBuilder,
    private loginService: LoginService,
    private router: Router
  ) {}

  ngOnInit() {
    this.loginForm = this.fb.group({
      email: new FormControl(
        "",
        Validators.compose([
          Validators.required,
          Validators.pattern(
            "^[a-zA-Z0-9][-a-zA-Z0-9._]+@([- a-z0-9]+[.])+[a-z]{2,5}$"
          ),
        ])
      ),
      password: new FormControl(
        "",
        Validators.compose([
          Validators.pattern(
            "(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&])[A-Za-zd$@$!%*?&].{8,}"
          ),
          ,
          Validators.required,
        ])
      ),
    });
  }

  //when user submits the login form
  loginUser() {
    this.isSubmitted = true;
    if (this.loginForm.valid) {
      this.loginService.loginUser(this.loginForm.value).subscribe((data) => {
        if (data.status === "success") {
          this.storeUserInfo(data.data).then((result) => {
            this.router.navigateByUrl("admin/dashboard");
          });
        }
      });
    }
  }

  //store user info in local storage
  async storeUserInfo(data) {
    await localStorage.setItem("mcny_user", JSON.stringify(data));
  }

  get errorControl() {
    return this.loginForm.controls;
  }
}
